                </button>

                <button type="button" class="btn btn-soft-success float-end" id="create-btn"
                        data-bs-toggle="modal" href="#agentModal" @click="addNewAgentModal"
                        style="white-space: nowrap; margin-right: 5px;">
                  <i class="ri-add-line align-bottom me-1"></i> Add Tech
                </button>
              </div>
          </div>
          </div>
          <!-- Table Head -->
          <span v-if="mobileView === false">
            <ul
                class="nav nav-tabs nav-tabs-custom nav-primary mb-3"
                role="tablist">
            <li class="nav-item">
              <a
                  class="nav-link active All py-3"
                  data-bs-toggle="tab"
                  id="All"
                  href="#home1"
                  role="tab"
                  aria-selected="true"
                  @click="updateAgentList(0)"
              >
                <i class="ri-file-search-line me-1 align-bottom"></i> All
              </a>
            </li>
             <li class="nav-item" v-for="(data, index) of statusList" :key="index">
              <a
                  class="nav-link py-3 " v-bind:class="data.name"
