              </a>
            </li>
            </ul>
            <div class="table-responsive">
              <table class="table align-middle table-nowrap mb-0 table-hover">
                <thead class="table-light">
                <tr>
                  <th scope="col" style="width: 50px">
                    <div class="form-check">
                      <input
                          class="form-check-input"
                          type="checkbox"
                          id="checkAll"
                          value="option"
                      />
                    </div>
                  </th>
                  <th scope="col">Name</th>
                  <th scope="col">Department</th>
                  <th scope="col">Email</th>
                  <th scope="col">Phone</th>
                  <th scope="col" v-if="this.data.searchValues.status === 2">Interview Date</th>
                  <th scope="col">Postal Code</th>
                  <th scope="col">Create Date</th>
                  <th scope="col">Status</th>
                  <th scope="col">Actions</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(data, index) of resultQuery" :key="index">
                  <td>
                    <div class="form-check">
                      <input class="form-check-input" type="checkbox" value="" id="cardtableCheck01">
                      <label class="form-check-label" for="cardtableCheck01"></label>
                    </div>
                  </td>
                  <td>{{ data.firstName }} {{ data.lastName }}</td>
                  <td>{{ data.departmentName }}</td>
                  <td>{{ data.email }}</td>
                  <td>{{ formatPhone(data.phoneNumber) }}</td>
                  <td v-if="this.data.searchValues.status === 2">{{
                      formatDate(data.interviewDate, 'MMMM Do YYYY, h:mmA')
                    }}</td>
                  <td>{{ data.postalCode }}</td>
                  <td> {{ formatDate(data.createdOn, 'MMMM Do YYYY, h:mmA') }} </td>
                  <td><a href="javascript:void(0);" v-bind:style="'color:'+data.color"
                         @click="filterStatus(data.statusName)">{{ data.statusName }}</a></td>
                  <td>
                    <ul class="list-inline hstack gap-2 mb-0">
                      <li class="list-inline-item" data-bs-toggle="modal" href="#agentModal" data-bs-trigger="hover"
                          data-bs-placement="top" title="Edit" @click="getUserInfo(data.ID)">
                        <a class="edit-item-btn" href="javascript:void(0);"><i
                            class="ri-pencil-fill align-bottom text-muted"></i></a>
                      </li>
                      <li class="list-inline-item" data-bs-toggle="modal" href="#open-notes" data-bs-trigger="hover"
                          data-bs-placement="top" title="Notes" @click="getNotes(data.ID)">
                        <a href="javascript:void(0);" class="text-muted d-inline-block">
                          <i class="ri-sticky-note-line fs-16"></i>
                        </a>
                      </li>
                      <li class="list-inline-item" data-bs-toggle="tooltip" data-bs-trigger="hover"
                          data-bs-placement="top" title="Delete" @click="deleteUser(data.ID)">
                        <a class="remove-item-btn" data-bs-toggle="modal" href="javascript:void(0);">
