                  </ul>
                  <a
                      class="page-item pagination-next"
                      href="#"
                      @click="page++"
                      v-if="page < pages.length"
                  >
                    Next
                  </a>
                </div>
              </div>
            </div>
          </span>
          <span v-else>
            <ul
                class="nav nav-tabs nav-tabs-custom nav-primary mb-3"
                role="tablist">
              <li class="nav-item">
                <a
                    class="nav-link active All py-3"
                    data-bs-toggle="tab"
                    id="All"
                    href="#home1"
                    role="tab"
                    aria-selected="true"
                    @click="updateAgentList(0)"
                >
                  <i class="ri-file-search-line me-1 align-bottom"></i> All
                </a>
              </li>
               <li class="nav-item" v-for="(data, index) of statusList" :key="index">
                <a
                    class="nav-link py-3 " v-bind:class="data.name"
                    data-bs-toggle="tab"
                    v-bind:id="data.name"
                    v-bind:href="'#'+data.name"
                    role="tab"
                    aria-selected="false"
                    @click="updateAgentList(data.statusID)"
                >
                  <i class="me-1 align-bottom"></i>
                  {{ data.name }}
                </a>
              </li>
            </ul>
            <div class="table-responsive">
              <table class="table align-middle table-nowrap mb-0 table-hover">
                <thead class="table-light">
                <tr>
                  <th scope="col">CARDS</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(data, index) of resultQuery" :key="index">
                  <td>
                    <div class="collapse show" :id="'leadDiscovered1'+data.ID">
                        <div class="card mb-1">
                            <div class="card-body">
                                <a class="d-flex align-items-center" data-bs-toggle="collapse"
                                   :href="'#leadDiscovered'+data.ID" role="button" aria-expanded="false"
                                   :aria-controls="'leadDiscovered'+data.ID">
                                    <div class="flex-shrink-0">
                                        <img :src="data.profileIMG" alt="" class="avatar-xs rounded-circle"/>
                                    </div>
                                    <div class="flex-grow-1 ms-3">
                                        <h6 class="fs-14 mb-1">{{ data.fullname }}</h6>
                                        <p class="text-muted mb-0">{{
                                            data.departmentName + ' - ' + data.statusName
                                          }}</p>
                                    </div>
                                </a>
                            </div>
                            <div class="collapse border-top border-top-dashed" :id="'leadDiscovered'+data.ID">
                                <div class="card-body">
                                    <h6 class="fs-14 mb-1">Nesta Technologies <small class="badge badge-soft-primary">4 Days</small></h6>
                                    <ul class="list-unstyled vstack gap-2 mb-0">
                                        <li>
                                            <div class="d-flex">
                                                <div class="flex-shrink-0 avatar-xxs text-muted">
                                                    <i class="ri-question-answer-line"></i>
                                                </div>
                                                <div class="flex-grow-1">
                                                    <h6 class="mb-0">Phone Number</h6>
                                                    <small class="text-muted"> {{ data.phoneNumber }}</small>
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <div class="d-flex">
                                                <div class="flex-shrink-0 avatar-xxs text-muted">
                                                    <i class="ri-mac-line"></i>
                                                </div>
                                                <div class="flex-grow-1">
                                                    <h6 class="mb-0">Email</h6>
                                                    <small class="text-muted">{{ data.email }}</small>
                                                </div>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                                <div class="card-footer hstack gap-2">
                                    <button class="btn btn-success btn-sm w-100 edit-item-btn" data-bs-toggle="modal"
                                            data-bs-trigger="hover" data-bs-placement="top" title="Edit"
                                            @click="getUserInfo(data.ID)" href="#agentModal"><i
                                        class="ri-pencil-fill align-bottom me-1"></i> Edit</button>
                                    <button class="btn btn-primary btn-sm w-100" data-bs-toggle="modal"
                                            href="#open-changePassword" data-bs-trigger="hover" data-bs-placement="top"
                                            title="Change Password" @click="setUserID(data.ID), getUserPermissions()"
                                            v-if="viewUserGroup('SUPER_ADMIN_GROUP')"><i
                                        class="ri-key-fill align-bottom me-1"></i> Change Password</button>
                                </div>
                            </div>
                        </div><!--end card-->
                    </div>
                  </td>
                  <!-- <td>{{ data.departmentName }}</td>-->
                  <!-- <td>{{ data.email }}</td>-->
                  <!-- <td>{{ formatPhone(data.phoneNumber) }}</td>-->
                  <!-- <td v-if="this.data.searchValues.status === 2">{{ formatDate(data.interviewDate, 'MMMM Do YYYY, h:mmA')  }}</td>-->
