5;"><bdt class="block-container if" data-type="if" style="text-align: left;"><bdt data-type="conditional-block"><bdt class="block-component" data-record-question-key="copyright_agent_option" data-type="statement"><bdt class="block-component"></bdt><bdt class="block-component"></bdt></bdt><bdt class="block-container if" data-type="if" style="text-align: left;"><bdt class="statement-end-if-in-editor" data-type="close"></bdt></bdt></bdt></bdt></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="block-component"></bdt><bdt class="block-container if" data-type="if" style="text-align: left;"><bdt class="statement-end-if-in-editor" data-type="close"><bdt class="block-component"></bdt></bdt></bdt></div><div class="MsoNormal" data-custom-class="heading_1" id="copyrightyes" style="line-height: 1.5; text-align: left;"><strong><span style="font-size: 19px;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;">14.</span></strong></span></strong></span>&nbsp;</strong>COPYRIGHT INFRINGEMENTS</span></strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">We respect the intellectual property rights of others. If you believe that any material available on or through the Services infringes upon any copyright you own or control, please immediately notify us using the contact information provided below (a <bdt class="block-component"></bdt>"Notification"<bdt class="statement-end-if-in-editor"></bdt>). A copy of your Notification will be sent to the person who posted or stored the material addressed in the Notification. Please be advised that pursuant to applicable law you may be held liable for damages if you make material misrepresentations in a Notification. Thus, if you are not sure that material located on or linked to by the Services infringes your copyright, you should consider first contacting an attorney.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="block-container if" data-type="if" style="text-align: left;"><bdt class="statement-end-if-in-editor" data-type="close"></bdt></bdt><bdt class="block-component"></bdt></div><div class="MsoNormal" data-custom-class="heading_1" id="terms" style="line-height: 1.5; text-align: left;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;">15.</span></strong></span></strong></span>&nbsp;</strong>TERM AND TERMINATION</span></strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">These Legal Terms shall remain in full force and effect while you use the Services. WITHOUT LIMITING ANY OTHER PROVISION OF THESE LEGAL TERMS, WE RESERVE THE RIGHT TO, IN OUR SOLE DISCRETION AND WITHOUT NOTICE OR LIABILITY, DENY ACCESS TO AND USE OF THE SERVICES (INCLUDING BLOCKING CERTAIN IP ADDRESSES), TO ANY PERSON FOR ANY REASON OR FOR NO REASON, INCLUDING WITHOUT LIMITATION FOR BREACH OF ANY REPRESENTATION, WARRANTY, OR COVENANT CONTAINED IN THESE LEGAL TERMS OR OF ANY APPLICABLE LAW OR REGULATION. WE MAY TERMINATE YOUR USE OR PARTICIPATION IN THE SERVICES OR DELETE <bdt class="block-container if" data-type="if" id="a6e121c2-36b4-5066-bf9f-a0a33512e768"><bdt data-type="conditional-block"><bdt class="block-component" data-record-question-key="user_account_option" data-type="statement"></bdt><bdt data-type="body">YOUR ACCOUNT AND&nbsp;</bdt></bdt><bdt class="statement-end-if-in-editor" data-type="close"></bdt></bdt>ANY CONTENT OR INFORMATION THAT YOU POSTED AT ANY TIME, WITHOUT WARNING, IN OUR SOLE DISCRETION.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">If we terminate or suspend your account for any reason, you are prohibited from registering and creating a new account under your name, a fake or borrowed name, or the name of any third party, even if you may be acting on behalf of the third party. In addition to terminating or suspending your account, we reserve the right to take appropriate legal action, including without limitation pursuing civil, criminal, and injunctive redress.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="heading_1" id="modifications" style="line-height: 1.5; text-align: left;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;">16.</span></strong></span></strong></span>&nbsp;</strong>MODIFICATIONS AND INTERRUPTIONS</span></strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">We reserve the right to change, modify, or remove the contents of the Services at any time or for any reason at our sole discretion without notice. However, we have no obligation to update any information on our Services.<bdt class="block-component"></bdt> We will not be liable to you or any third party for any modification, price change, suspension, or discontinuance of the Services.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">We cannot guarantee the Services will be available at all times. We may experience hardware, software, or other problems or need to perform maintenance related to the Services, resulting in interruptions, delays, or errors. We reserve the right to change, revise, update, suspend, discontinue, or otherwise modify the Services at any time or for any reason without notice to you. You agree that we have no liability whatsoever for any loss, damage, or inconvenience caused by your inability to access or use the Services during any downtime or discontinuance of the Services. Nothing in these Legal Terms will be construed to obligate us to maintain and support the Services or to supply any corrections, updates, or releases in connection therewith.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="heading_1" id="law" style="line-height: 1.5; text-align: left;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;">17.</span></strong></span></strong></span>&nbsp;</strong>GOVERNING LAW</span></strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);"><bdt class="block-component"></bdt></span></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);">These Legal Terms and your use of the Services are governed by and construed in accordance with the laws of <bdt class="block-container if" data-type="if" id="b86653c1-52f0-c88c-a218-e300b912dd6b"><bdt data-type="conditional-block"><bdt class="block-component" data-record-question-key="governing_law" data-type="statement"></bdt><bdt data-type="body">the State of <bdt class="block-container question question-in-editor" data-id="b61250bd-6b61-32ea-a9e7-4a02690297c3" data-type="question">Texas</bdt></bdt></bdt><bdt class="statement-end-if-in-editor" data-type="close"></bdt></bdt> applicable to agreements made and to be entirely performed within<bdt class="block-container if" data-type="if" id="b86653c1-52f0-c88c-a218-e300b912dd6b" style="font-size: 14.6667px;"><bdt data-type="conditional-block">&nbsp;<span style="font-size: 11pt; line-height: 16.8667px; color: rgb(89, 89, 89);"><bdt class="block-container if" data-type="if" id="b86653c1-52f0-c88c-a218-e300b912dd6b"><bdt data-type="conditional-block"><bdt class="block-component" data-record-question-key="governing_law" data-type="statement"></bdt><bdt data-type="body">the State of <bdt class="block-container question question-in-editor" data-id="b61250bd-6b61-32ea-a9e7-4a02690297c3" data-type="question">Texas</bdt></bdt></bdt><bdt class="statement-end-if-in-editor" data-type="close"></bdt></bdt><span style="font-size: 14.6667px;">,&nbsp;</span>without regard to its conflict of law principles.<bdt class="block-component"></bdt></span></bdt></bdt></span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="heading_1" id="disputes" style="line-height: 1.5; text-align: left;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 24.5333px; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;"><strong><span style="line-height: 115%; font-family: Arial; font-size: 19px;">18.</span></strong></span></strong></span>&nbsp;</strong>DISPUTE RESOLUTION</span></strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="block-component"></bdt></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="block-component"></bdt></div><div class="MsoNormal" data-custom-class="heading_2" style="line-height: 1.5; text-align: left;"><strong>Informal Negotiations</strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 15px;">To expedite resolution and control the cost of any dispute, controversy, or claim related to these Legal Terms (each a <bdt class="block-component"></bdt>"Dispute" and collectively, the "Disputes"<bdt class="statement-end-if-in-editor"></bdt>) brought by either you or us (individually, a <bdt class="block-component"></bdt>"Party" and collectively, the "Parties"<bdt class="statement-end-if-in-editor"></bdt>), the Parties agree to first attempt to negotiate any Dispute (except those Disputes expressly provided below) informally for at least <bdt class="question">one (1)</bdt> days before initiating arbitration. Such informal negotiations commence upon written notice from one Party to the other Party.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="statement-end-if-in-editor"></bdt></div><div class="MsoNormal" data-custom-class="heading_2" style="line-height: 1.5; text-align: left;"><strong>Binding Arbitration</strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><bdt class="block-component"><span style="font-size: 15px;"></span></bdt><span style="font-size: 15px;">If the Parties are unable to resolve a Dispute through informal negotiations, the Dispute (except those Disputes expressly excluded below) will be finally and exclusively resolved by binding arbitration. YOU UNDERSTAND THAT WITHOUT THIS PROVISION, YOU WOULD HAVE THE RIGHT TO SUE IN COURT AND HAVE A JURY TRIAL. The arbitration shall be commenced and conducted under the Commercial Arbitration Rules of the American Arbitration Association (<bdt class="block-component"></bdt>"AAA"<bdt class="statement-end-if-in-editor"></bdt>) and, where appropriate, the AAAs Supplementary Procedures for Consumer Related Disputes (<bdt class="block-component"></bdt>"AAA Consumer Rules"<bdt class="statement-end-if-in-editor"></bdt>), both of which are available at the <span style="font-size: 15px; line-height: 16.8667px; color: rgb(89, 89, 89);"><a data-custom-class="link" href="http://www.adr.org" rel="noopener noreferrer" target="_blank">American Arbitration Association (AAA) website</a></span>. Your arbitration fees and your share of arbitrator compensation shall be governed by the AAA Consumer Rules and, where appropriate, limited by the AAA Consumer Rules. <bdt class="block-component"></bdt>If such costs are determined by the arbitrator to be excessive, we will pay all arbitration fees and expenses. <bdt class="statement-end-if-in-editor"></bdt>The arbitration may be conducted in person, through the submission of documents, by phone, or online. The arbitrator will make a decision in writing, but need not provide a statement of reasons unless requested by either Party. The arbitrator must follow applicable law, and any award may be challenged if the arbitrator fails to do so. Except where otherwise required by the applicable AAA rules or applicable law, the arbitration will take place in <bdt class="block-component"></bdt><bdt class="question">United States Of America</bdt>, <bdt class="statement-end-if-in-editor"></bdt><bdt class="block-component"></bdt><bdt class="question">Texas</bdt><bdt class="statement-end-if-in-editor"></bdt>. Except as otherwise provided herein, the Parties may litigate in court to compel arbitration, stay proceedings pending arbitration, or to confirm, modify, vacate, or enter <bdt class="block-component"></bdt>judgment<bdt class="statement-end-if-in-editor"></bdt> on the award entered by the arbitrator.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 15px;">If for any reason, a Dispute proceeds in court rather than arbitration, the Dispute shall be commenced or prosecuted in the</span><bdt class="block-component" style="font-size: 15px;"></bdt><span style="font-size: 15px;">&nbsp;state and federal courts</span><bdt class="statement-end-if-in-editor" style="font-size: 15px;"></bdt><span style="font-size: 15px;">&nbsp;located in</span><bdt class="block-component" style="font-size: 15px;"></bdt> <bdt class="question">United States Of America</bdt><span style="font-size: 15px;">,</span><bdt class="statement-end-if-in-editor" style="font-size: 15px;"></bdt> <bdt class="block-component" style="font-size: 15px;"></bdt><bdt class="question" style="font-size: 15px;">Texas</bdt><bdt class="statement-end-if-in-editor" style="font-size: 15px;"></bdt><span style="font-size: 15px;">, and the Parties hereby consent to, and waive all <bdt class="block-component"></bdt>defenses<bdt class="statement-end-if-in-editor"></bdt> of lack of personal jurisdiction, and forum non conveniens with respect to venue and jurisdiction in such<bdt class="block-component"></bdt> state and federal courts<bdt class="statement-end-if-in-editor"></bdt>. Application of the United Nations Convention on Contracts for the International Sale of Goods and the Uniform Computer Information Transaction Act (UCITA) are excluded from these Legal Terms.</span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><span style="font-size: 15px;"><bdt class="block-component"></bdt>In no event shall any Dispute brought by either Party related in any way to the Services be commenced more than <bdt class="question">one (1)</bdt> years after the cause of action arose. <bdt class="statement-end-if-in-editor"></bdt>If this provision is found to be illegal or unenforceable, then neither Party will elect to arbitrate any Dispute falling within that portion of this provision found to be illegal or unenforceable and such Dispute shall be decided by a court of competent jurisdiction within the courts listed for jurisdiction above, and the Parties agree to submit to the personal jurisdiction of that court.<bdt class="block-component"></bdt></span></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="heading_2" style="line-height: 1.5; text-align: left;"><strong>Restrictions</strong></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;">The Parties agree that any arbitration shall be limited to the Dispute between the Parties individually. To the full extent permitted by law, (a) no arbitration shall be joined with any other proceeding; (b) there is no right or authority for any Dispute to be arbitrated on a class-action basis or to <bdt class="block-component"></bdt>utilize<bdt class="statement-end-if-in-editor"></bdt> class action procedures; and (c) there is no right or authority for any Dispute to be brought in a purported representative capacity on behalf of the general public or any other persons.</div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="heading_2" style="line-height: 1.5; text-align: left;"><bdt class="block-component"></bdt><strong>Exceptions to Informal Negotiations and Arbitration</strong><bdt class="statement-end-if-in-editor"></bdt></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="body_text" style="line-height: 1.5; text-align: left;"><bdt class="block-component"></bdt>The Parties agree that the following Disputes are not subject to the above provisions concerning informal negotiations binding arbitration: (a) any Disputes seeking to enforce or protect, or concerning the validity of, any of the intellectual property rights of a Party; (b) any Dispute related to, or arising from, allegations of theft, piracy, invasion of privacy, or <bdt class="block-component"></bdt>unauthorized<bdt class="statement-end-if-in-editor"></bdt> use; and (c) any claim for injunctive relief. If this provision is found to be illegal or unenforceable, then neither Party will elect to arbitrate any Dispute falling within that portion of this provision found to be illegal or unenforceable and such Dispute shall be decided by a court of competent jurisdiction within the courts listed for jurisdiction above, and the Parties agree to submit to the personal jurisdiction of that court.<bdt class="statement-end-if-in-editor"></bdt></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><bdt class="statement-end-if-in-editor"><bdt class="statement-end-if-in-editor"></bdt></bdt></div><div class="MsoNormal" style="line-height: 1.5; text-align: left;"><br></div><div class="MsoNormal" data-custom-class="head