<script>
import animationData from "@/components/widgets/msoeawqm.json";

export default {
  props:['workLoads', 'labels'],
  components: {
  },
  data(){
    return{
      defaultOptions: { animationData: animationData },
      chartOptions: {
        // fixed with locations not he correct solution but i's the easier
        labels: ["Albuquerque", "Corpus Christi", "Houston"],
        chart: {
          height: 444,
          type: "donut",
        },
        legend: {
          position: "bottom",
        },
        stroke: {
          show: false,
        },
        dataLabels: {
          dropShadow: {
            enabled: false,
          },
        },
        colors: this.getChartColorsArray(
            '["--vz-primary", "--vz-primary-rgb, 0.85", "--vz-primary-rgb, 0.70", "--vz-primary-rgb, 0.60", "--vz-primary-rgb, 0.45"]'
        )
      },
      series: [],
    }
  },
  methods:{
    getChartColorsArray(colors) {
      colors = JSON.parse(colors);
      return colors.map(function (value) {
        var newValue = value.replace(" ", "");
        if (newValue.indexOf(",") === -1) {
          var color = getComputedStyle(document.documentElement).getPropertyValue(newValue);
          if (color) {
            color = color.replace(" ", "");
            return color;
          } else return newValue;
        } else {
          var val = value.split(',');
          if (val.length === 2) {
            var rgbaColor = getComputedStyle(document.documentElement).getPropertyValue(val[0]);
            rgbaColor = "rgba(" + rgbaColor + "," + val[1] + ")";
            return rgbaColor;
          } else {
            return newValue;
          }
        }
      });
    },
  },
  mounted() {
  },
  watch: {
    workLoads: function () {
      // let qty = parseInt(this.workLoads['qty'])
      // let workload = [];

      // for (var i in this.workLoads['qtyByLocation']) {
      //   workload.push(parseInt(((parseInt(this.workLoads['qtyByLocation'][i]) / qty) * 100).toFixed(2)))
      // }

      // this.series = workload;
      this.series = this.workLoads['qtyByLocation'];
    },
    labels: function () {
      this.chartOptions.labels = this.labels
    },
  }
};
</script>

<template>
    <div class="col-xl-4 col-sm-12">
      <div class="card card-height-100">
        <div class="card-header align-items-center d-flex">
          <h4 class="card-title mb-0 flex-grow-1">
            Work Load By Locations
          </h4>
        </div>
        <!-- end card header -->
        <div class="card-body">
          <apexchart class="apex-charts" dir="ltr" :options="chartOptions" :series="series" ></apexchart>
        </div>
      </div>
      <!-- .card-->
    </div>
</template>>
